<template>
    <div>
        <list :list="list" v-if="list.length"></list>
        <div class="nonode" v-else>
            没有您要搜索的内容
            <a href="#" class="comeback" @click="comeback">请返回</a>
        </div>
    </div>
</template>


<script>
import $ajax from '../plugins/ajax'
export default {
    data(){
        return{
            list:[]
        }
    },
    beforeRouteEnter:async (to,from,next)=>{
        var res=await $ajax("/search?word="+to.query.word);
        next(vm=>{
            vm.list=res.data;
        })
    },
    beforeRouteUpdate:async function(to,from,next){
        var res=await $ajax("/search?word="+to.query.word);
        this.list=res.data;
        next();
    },
    methods:{
        comeback(){
            this.$router.back()


        }
    }
}
</script>
<style scoped>
    .nonode{
        text-align: center;
        line-height: 400px;
    }
    .comeback{
        color:blue
    }
</style>